<template>
  <div class="discovery-container">
    <!-- 轮播图 -->
    <el-carousel :interval="4000" type="card">
      <el-carousel-item v-for="(item, index) in banners" :key="index">
        <img :src="item.imageUrl" alt="">
      </el-carousel-item>
    </el-carousel>

    <!-- 推荐歌单 -->
    <div class="recommend">
      <h3 class="title">推荐歌单</h3>
      <div class="items">
        <div class="item" v-for="(item,index) in list" :key="index">
          <div class="img-wrap">
            <div class="desc-wrap">
              <span class="desc">{{item.copywriter}} </span>
            </div>
            <img :src="item.picUrl" alt="">
            <span class="iconfont icon-play"></span>
          </div>
          <p class="name">{{item.name}}</p>
        </div>
      </div>
    </div>

    <!-- 最新 -->
    <div class="news">
      <h3 class="title">最新音乐</h3>
      <div class="items">
        <div class="item" v-for="(item,index) in songs" :key="index">
          <div class="img-wrap">
            <img :src="item.picUrl" alt="">
            <span class="iconfont icon-play"></span>
          </div>
          <div class="song-wrap">
          <span class="song-name">{{item.name}}</span>
            <span class="singer">{{item.song.artists[0].name}} </span>
          </div>
        </div>
      </div>
    </div>

    <!-- 推荐mv -->
    <div class="mvs">
      <h3 class="title">推荐mv</h3>
      <div class="items">
        <div class="item" v-for="(item,index) in mvs" :key="index">
          <div class="img-wrap">
            <img :src="item.picUrl" alt="">
            <span class="iconfont icon-play"></span>
            <div class="num-wrap">
              <div class="iconfont icon-play"></div>
              <div class="num">{{item.playCount}} </div>
            </div>
          </div>
          <div class="info-wrap">
            <div class="name">{{item.name}} </div>
            <div class="singer">{{item.artistName}} </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import Father from '../component/1.father'
// import Bottom from '../components/03.bottom'
export default {
  components: {
    // Bottom,
    // Father
  },
  data () {
    return {
      // 轮播图
      banners: [],
      // 推荐歌单
      list: [],
      // 最新音乐
      songs: [],
      // 推荐mv
      mvs: []
    }
  },
  created () {
    //轮播图
    this.FFaxios({
      url: 'https://autumnfish.cn/banner',
      method: 'get'
    }).then(res => {
      // console.log(res);
      this.banners = res.data.banners
      // console.log(this.banners)
    })

    //推荐歌单
    this.FFaxios({
      url: 'https://autumnfish.cn/personalized',
      method: 'get',
      params: {
        //获取的数量
        limit: 10
      }
    }).then(res => {
      // console.log(res);
      this.list = res.data.result
      // console.log(this.list)
    })

    //最新音乐
    this.FFaxios({
      url: 'https://autumnfish.cn/personalized/newsong',
      method: 'get'
    }).then(res => {
      // console.log(res);
      this.songs = res.data.result
      console.log(this.songs)
    })

    //mv
    this.FFaxios({
      url: 'https://autumnfish.cn/personalized/mv',
      method: 'get'
    }).then(res => {
      // console.log(res);
      this.mvs = res.data.result
    })
  },
  methods: {
    playMusic (id) {
      this.FFaxios({
        url: 'https://autumnfish.cn/song/url',
        methods: 'get',
        params: {
          id
        }
      }).then(res => {
        // console.log(res);
        let url = res.data.data[0].url
        // console.log(url);//为mp3 文件链接
        if (!url) {
          return this.$message.error('该资源为VIP专享,暂不支持播放!')
        }
        //设置给父组件的播放地址
        this.$parent.musicUrl = url
      })
    },
    toMv () {
      this.$router.push({ path: '/mv' })
    },
    toList () {
      this.$router.push({ path: '/playlist' })
    }
  }
}
</script>
<style lang='scss'>
.discovery-container {
  .el-carousel__container {
    height: 230px;
  }
  .el-carousel__item img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }
  /* 推荐歌单 */
  .recommend {
    .title {
      font-weight: normal;
      margin-bottom: 20px;
      padding-left: 8px;
    }
    .items {
      display: flex;
      flex-wrap: wrap;
      .item {
        width: 200px;
        margin: 10px;
        position: relative;
        overflow: hidden;
        .img-wrap {
          position: relative;
          &:hover {
            .icon-play {
              opacity: 1;
            }
            .desc-wrap {
              top: 0;
            }
          }
          .desc-wrap {
            position: absolute;
            width: 100%;
            top: 0;
            left: 0;
            font-size: 16px;
            color: white;
            background: rgba(0, 0, 0, 0.5);
            max-height: 50px;
            padding: 5px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            top: -50px;
            .desc {
              font-size: 14px;
            }
          }
          img {
            width: 200px;
            height: 200px;
            border-radius: 5px;
          }
          .icon-play {
            position: absolute;
            right: 10px;
            bottom: 13px;
            width: 40px;
            height: 40px;
            color: #dd6d60;
            font-size: 20px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            background: rgba(255, 255, 255, 0.8);
            opacity: 0;
            &::before {
              transform: translateX(3px);
            }
          }
        }
        .name {
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          font-size: 14px;
        }
      }
    }
  }
  /* 最新音乐 */
  .news {
    .title {
      font-weight: normal;
      margin-bottom: 20px;
      padding-left: 8px;
    }
    .items {
      height: 500px;
      display: flex;
      padding-left: 15px;
      flex-direction: row;
      flex-wrap: wrap;
      .item {
        padding: 0;
        margin: 0;
        height: 100px;
        width: 50%;
        display: flex;
        align-items: center;
        &:hover {
          background-color: #f5f5f5;
        }
        .index {
          margin-right: 15px;
          font-size: 15px;
        }
        .img-wrap {
          margin: 0;
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          &:hover {
            .iconfont {
              opacity: 1;
            }
          }
          img {
            width: 80px;
            height: 80px;
          }
          .iconfont {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 30px;
            height: 30px;
            color: #dd6d60;
            text-align: center;
            line-height: 30px;
            border-radius: 50%;
            font-size: 14px;
            background: rgba(255, 255, 255, 0.8);
            opacity: 0;
            &::before {
              transform: translateX(3px);
            }
          }
        }
        .song-wrap {
          margin: 0;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          height: 100%;
          padding: 10px;
          font-size: 16px;
          .signer {
            color: gray;
            font-size: 14px;
          }
        }
      }
    }
  }
  /* 推荐MV */
  .mvs {
    .title {
      font-weight: normal;
      margin-bottom: 20px;
      padding-left: 8px;
    }
    .items {
      display: flex;
      justify-content: space-around;
      .item {
        width: 250px;
        cursor: pointer;
        .img-wrap {
          width: 100%;
          position: relative;
          &:hover {
            .icon-play {
              opacity: 1;
            }
          }
          img {
            width: 100%;
            border-radius: 5px;
          }
          .icon-play {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 40px;
            height: 40px;
            color: #dd6d60;
            font-size: 20px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba(255, 255, 255, 0.8);
            opacity: 0;
            &::before {
              transform: translateX(3px);
            }
          }
          .num-wrap {
            position: absolute;
            color: white;
            top: 0;
            right: 0;
            display: flex;
            align-content: center;
            font-size: 15px;
            padding-right: 5px;
            padding-top: 2px;
            .icon-play {
              font-size: 12px;
              display: flex;
              align-items: center;
              margin-right: 5px;
              padding-right: 70px;
              opacity: 1;
              color: white;
              background-color: rgba(255, 255, 255, 0);
            }
          }
        }
        .info-wrap {
          .name {
            font-size: 15px;
            margin-bottom: 5px;
          }
          .singer {
            font-size: 14px;
            color: #c5c5c5;
          }
        }
      }
    }
  }
}
</style>